@use '../variables/variables-colors' as bf-v-color;
@use '../variables/variables-texts' as bf-v-text;
@use '../variables/variables-spacing' as bf-v-space;
@use '../variables/variables-breakpoints' as bf-v-bp;
@use 'sass:color';

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0; // fallback
  font-weight: 600;
  font-family: inherit;
  color: inherit;
  line-height: 1.25;
  margin-bottom: bf-v-space.$bf-spacing-lg-rem;
}

h1 {
  font-size: 36px;
  font-size: 3.6rem;
}

h2 {
  font-size: 30px;
  font-size: 3rem;
}

h3 {
  font-size: 24px;
  font-size: 2.4rem;
}

h4 {
  font-size: 20px;
  font-size: 2rem;
}

h5,
h6 {
  font-size: 16px;
  font-size: 1.6rem;
}
